.contact-form {
  position: relative;
  border: none;
  padding: 0 20px;
  margin: 0;
  margin-top: 64px;
}

.contact-form__title {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

.contact-form__name {
  display: block;
  font-size: 14px;
  color: #333333;
}

.contact-form__input {
  width: 264px;
  height: 58px;
  background-image: linear-gradient(to right, #f2f2f2 222px, #e6e6e6 222px, #e6e6e6 280px);
  border: none;
  padding-left: 16px;
  margin-bottom: 16px;
}

.contact-form__label--phone::before {
  background-image: url("/img/images/form/phone.svg");
  background-repeat: no-repeat;
  position: absolute;
  left: 265px;
  top: 42px;
  content: "";
  width: 18px;
  height: 18px;
  display: block;
}

.contact-form__label--mail::before {
  background-image: url("/img/images/form/mail.svg");
  background-repeat: no-repeat;
  position: absolute;
  left: 286px;
  top: 140px;
  content: "";
  width: 18px;
  height: 18px;
  display: block;
}

@media (min-width: $tablet-width) {
  .contact-form {
    width: 343px;
    margin-top: 53px;
    padding: 0;
  }

  .contact-form__title {
    font-size: 21px;
  }

  .contact-form__label {
    display: block;
    margin-left: 44px;
  }

  .contact-form__label--phone::before {
    left: 285px;
  }
}

@media (min-width: $desktop-width) {
  .contact-form {
    width: 543px;
    margin-top: 53px;
  }

  .contact-form__label {
    margin-left: 100px;
  }

  .contact-form__input {
    width: 402px;
    height: 58px;
    background-image: linear-gradient(to right, #f2f2f2 354px, #e6e6e6 354px, #e6e6e6 402px);
    padding-left: 16px;
    margin-bottom: 16px;
  }

  .contact-form__label--phone::before {
    left: 475px;
    top: 40px;
  }

  .contact-form__label--mail::before {
    left: 480px;
  }
}
